
<Page name="settings">
  <Navbar>
    <NavLeft backLink="Back" />
    <NavTitle>Currency Converter</NavTitle>
    <NavRight>
      <Link text="Add" on:click={()=>{openEditPopup();}} />
    </NavRight>
  </Navbar>

  {#if thisItemList.length > 0}
  <BlockTitle small><span style="color:rgba(0,0,0,0.25);">swipe left to delete, right to edit</span></BlockTitle>
  <List mediaList>
    {#each thisItemList as item}
      <ListItem
        noChevron
        swipeout
        title={`${item.from} to ${item.to}`}
        after={item.rate}>
        <SwipeoutActions right>
          <SwipeoutButton
            bgColor="red"
            close
            overswipe
            onClick={()=>{deleteConverter(item);}}>
            <Icon f7="trash_fill" />
            Delete
          </SwipeoutButton>
        </SwipeoutActions>
        <SwipeoutActions left>
          <SwipeoutButton
            bgColor="blue"
            close
            overswipe
            onClick={()=>{openEditPopup(item);}}>
            <Icon f7="square_pencil" />
            Edit
          </SwipeoutButton>
        </SwipeoutActions>
      </ListItem>
    {/each}
  </List>
  {/if}

  <Popup bind:this={editItemPopup}>
      <Page>
        <Navbar title="Converter">
          <NavRight>
            <!-- <Link popupClose>Close</Link> -->
            <Link on:click={()=>{ editItemPopup.close() }}>Close</Link>
          </NavRight>
        </Navbar>
        <List>
          <ListInput
            label="Currency (From)"
            type="text"
            placeholder="e.g. USD"
            value={editFrom}
            onInput={(e)=>editFrom=e.target.value}
          />
          <ListInput
            label="Currency (To)"
            type="text"
            placeholder="e.g. HKD"
            value={editTo}
            onInput={(e)=>editTo=e.target.value}
          />
          <ListInput
            label="Rate"
            type="number"
            placeholder="e.g. 7.75"
            value={editRate}
            onInput={(e)=>editRate=e.target.value}
          />        
        </List>
        <List>
          <ListButton title="Save" on:click={()=>{saveEditPopup()}} />
        </List>
      </Page>
  </Popup>
</Page>



<script>
  import {
    f7,
    Page,
    Navbar,
    List,
    ListInput,
    ListItem,
    ListButton,
    Toggle,
    BlockTitle,
    BlockHeader,
    Row,
    Button,
    Range,
    Block,
    NavLeft,
    NavRight,
    NavTitle,
    NavTitleLarge,
    Link,
    SwipeoutActions,
    SwipeoutButton,
    Icon,
    Popup,
  } from "framework7-svelte";

  import { currencyConverterDataList, updateCurrencyConverterData } from '../js/data';

  let thisItemList = JSON.parse(JSON.stringify($currencyConverterDataList));
  
  let editItemPopup;
  let editFrom;
  let editTo;
  let editRate;
  let editItem;
  let editIsNew = false;

  function openEditPopup(item = null){
      editIsNew = false;
      if(item != null){
            editItem = item;
            editFrom = item.from;
            editTo = item.to;
            editRate = item.rate;
      }
      else{ //new
            editIsNew = true;
            editItem = {};            
            editFrom = null;
            editTo = null;
            editRate = null;
      }
      editItemPopup.open();
  }

  async function saveEditPopup(){
      let preloader = f7.dialog.preloader();
      editItem.from = editFrom;
      editItem.to = editTo;
      editItem.rate = editRate;
      if(editIsNew)
        thisItemList.push(editItem);

      let result = await updateCurrencyConverterData(thisItemList);
      preloader.close();
      if(result.error){
        f7.dialog.alert(result.message, 'Alert');
      }
      editItemPopup.close();
      thisItemList = JSON.parse(JSON.stringify($currencyConverterDataList));
  }

  function deleteConverter(item){
      f7.dialog.confirm('', 'Delete?', async ()=>{
        let preloader = f7.dialog.preloader();
        thisItemList.splice(thisItemList.indexOf(item),1);
        let result = await updateCurrencyConverterData(thisItemList);
        preloader.close();
        if(result.error){
            f7.dialog.alert(result.message, 'Alert');
        }
        thisItemList = JSON.parse(JSON.stringify($currencyConverterDataList));
      });
  }

  
//   [
//       {from: 'HKD', to: 'MOP', rate: 1.03},
//       {from: 'USD', to: 'MOP', rate: 7.97},
//       {from: 'RMB', to: 'MOP', rate: 1.11},
//   ];
</script>
