import { writable, get } from 'svelte/store';
import auth from './auth';

let dataVersion = null;

export var dailyData = {};
export var monthlyData = {};
export var categoryData = {};
export var searchHistoryData = [];
export var itemHistoryData = [];
export var monthlyReportData = {};
export var currencyConverterData = [];

export const dailyDataList = writable([]);
export const monthlyDataList = writable([]);
export const categoryDataList = writable([]);
export const searchHistoryDataList = writable([]);
export const itemHistoryDataList = writable([]);
export const currencyConverterDataList = writable([]);

var db = firebase.firestore();


export function getItemByDate(dateId) {
  let thisItems = get(dailyDataList);
  for (let i = 0; i < thisItems.length; i++) {
    if (thisItems[i].dateId == dateId)
      return thisItems[i];
  }
  return null;
}


let lastFetchDateId = null;
const FETCH_AMOUNT = 5;

export var noMoreData = false;


export function fetchData({fromDate = null, toDate = null, search = null, categories = null} = {}, refresh = false){

  //add search history
  if(refresh && search != null){
    searchHistoryData.unshift(search.trim());
    searchHistoryData = [...new Set(searchHistoryData)];
    searchHistoryDataList.set(searchHistoryData);
    db.collection("users").doc(auth.firebaseUser.uid).set(
      {
        searchHistoryData: searchHistoryData
      },
      {merge: true}
    )
  }

  let oldLastFetchDateId = lastFetchDateId;
  let oldNoMoreData = noMoreData;
  if(refresh){
    lastFetchDateId = null;
    noMoreData = false;
  }

  return new Promise(resolve=>{
    db.collection("users").doc(auth.firebaseUser.uid).get()
    .then(
      function(doc){
        if(doc.data()){   
          if(refresh){
            dataVersion = doc.data().dataVersion || null;

            searchHistoryData = doc.data().searchHistoryData || [];
            searchHistoryDataList.set(searchHistoryData);
  
            itemHistoryData = doc.data().itemHistoryData || [];
            itemHistoryDataList.set(itemHistoryData);

            currencyConverterData = doc.data().currencyConverterData || [];
            currencyConverterDataList.set(currencyConverterData);
          }          

          categoryData = JSON.parse(doc.data().categoryData || '{}');
          makeCategoryDataList();

          monthlyData = doc.data().monthlyData || {};
          makeMonthlyDataList();

          let q = db.collection("users").doc(auth.firebaseUser.uid).collection("dailyData");

          if(lastFetchDateId != null){
            q = q.where("dateId", "<", lastFetchDateId);
          }

          if(search != null && search.length > 0){
            let tags = stringToTags(search);
            if(tags.length > 10){
              tags.length = 10;
            }
            q = q.where("tags", "array-contains-any", tags);
          }

          if(categories != null && categories.length > 0){
            q = q.where("categories", "array-contains-any", categories);
          }

          if(fromDate != null && toDate == null){
            q = q.where("dateId", ">=", fromDate);
            q = q.where("dateId", "<=", fromDate);
          }
          else if(fromDate != null && toDate != null){
            q = q.where("dateId", ">=", fromDate);
            q = q.where("dateId", "<=", toDate);
          }

          q = q.orderBy("dateId", "desc");

          q = q.limit(FETCH_AMOUNT);

          q.get()
          .then(function(querySnapshot) {
            if(refresh){
              dailyData = {};
            }

            let i = 0;
            querySnapshot.forEach(function(doc) {
              dailyData[`${doc.id}`] = doc.data();
              lastFetchDateId = doc.data().dateId;
              i++;
            });
            if(i == 0 || i < FETCH_AMOUNT) noMoreData = true;
            makeDailyDataList();
            resolve();
          })
          .catch(function(error) {
            lastFetchDateId = oldLastFetchDateId;
            noMoreData = oldNoMoreData;
            console.log("Error getting documents: ", error);
            resolve();
          });
        }
      }
    )
    .catch(function(error) {
      lastFetchDateId = oldLastFetchDateId;
      noMoreData = oldNoMoreData;
      console.log("Error getting document:", error);
      resolve();
    });
  });  
}

export function fetchMonthlyReportData(monthId, refresh = false){
  return new Promise(resolve=>{

    if(!refresh && monthlyReportData.hasOwnProperty(monthId+'')){
      return resolve({
        error: false,
        data: monthlyReportData[monthId+''],
      });
    }

    let strSearch = monthId;
    let strlength = strSearch.length;
    let strFrontCode = strSearch.slice(0, strlength-1);
    let strEndCode = strSearch.slice(strlength-1, strSearch.length);
    let startcode = strSearch;
    let endcode= strFrontCode + String.fromCharCode(strEndCode.charCodeAt(0) + 1);

    db.collection("users").doc(auth.firebaseUser.uid).collection("dailyData")
    .where('dateId', '>=', startcode)
    .where('dateId', '<', endcode)
    .get()
    .then(
      function(querySnapshot) {
        monthlyReportData[monthId+''] = {};
        querySnapshot.forEach(function(doc) {
          monthlyReportData[monthId+''][`${doc.id}`] = doc.data();
        });
        resolve({
          error: false,
          data: monthlyReportData[monthId+''],
        });
      }
    )
    .catch(function(error) {
      resolve({
        error: true,
        message: `Error getting documents: , ${error}`
      });
    });    
  }); 
}

function checkDataVersion(){
  return new Promise(resolve=>{
    db.collection("users").doc(auth.firebaseUser.uid).get()
    .then(
      function(doc){
        if(doc.data()){
          // dataVersion = doc.data().dataVersion || null;
          let result = dataVersion === (doc.data().dataVersion || null);
          let message = null;
          if(!result)
            message = 'Data is not up-to-date, please reload the app before update';
          resolve({
            error: false,
            result: result,
            message: message,
          });
        }
      }
    )
    .catch(function(error) {
      resolve({
        error: true,
        message: 'Something went wrong, please try again later',
        result: false,
      });
    });
  });  
}

export function editItem(clonedDailyDataItem, difference, editItemObj = null){
  return new Promise(async resolve =>{

    let checkDataVersionResult = await checkDataVersion();
    if(!checkDataVersionResult.result){
      return resolve({
        error: true,
        message: checkDataVersionResult.message,
      });
    }

    let newDataVersion = `${(new Date()).getTime()}-${Math.floor(Math.random() * (99999 - 10000 + 1) + 10000)}`;

    let clonedMonthlyData = JSON.parse(JSON.stringify(monthlyData));
    let monthId = moment(new Date(clonedDailyDataItem.dateTime)).format('YYYYMM');
    clonedMonthlyData[`${monthId}`] = (parseFloat(clonedMonthlyData[`${monthId}`]) || 0) + difference;
    clonedMonthlyData[`${monthId}`] = parseFloat(clonedMonthlyData[`${monthId}`]);

    let clonedDailyData = JSON.parse(JSON.stringify(dailyData));
    let dateId = moment(new Date(clonedDailyDataItem.dateTime)).format('YYYYMMDD');
    clonedDailyData[`${dateId}`] = clonedDailyDataItem;
    clonedDailyData[`${dateId}`].tags = generateTags(clonedDailyData[`${dateId}`].items);
    clonedDailyData[`${dateId}`].categories = generateCategories(clonedDailyData[`${dateId}`].items);

    //is add item
    if(editItemObj != null){
      for(let i = 0; i < itemHistoryData.length; i++){
        if(itemHistoryData[i].name === editItemObj.name){
          itemHistoryData.splice(i, 1);
          break;
        }
      }
      itemHistoryData.unshift({name: editItemObj.name, price: editItemObj.price, categoryIdList: editItemObj.categoryIdList});
      itemHistoryDataList.set(itemHistoryData);
    }  

    db.collection("users").doc(auth.firebaseUser.uid).set({
      monthlyData: clonedMonthlyData,
      dataVersion: newDataVersion,
    }, {merge: true})
    .then(function() {
      db.collection("users").doc(auth.firebaseUser.uid).collection("dailyData").doc(dateId).set(clonedDailyData[`${dateId}`])
        .then(function() {
          dataVersion = newDataVersion;
          monthlyData = clonedMonthlyData;
          dailyData = clonedDailyData;

          makeDailyDataList();
          makeMonthlyDataList();

          resolve({
            error: false,
            message: null,
          });
        })
        .catch(function(error) {
          resolve({
            error: true,
            message: "Error writing document: " + error
          });
        });
      })
    .catch(function(error) {
      resolve({
        error: true,
        message: "Error writing document: " + error
      });
    });
  });
}

export function deleteItem(clonedDailyDataItem, difference){
  return new Promise(async resolve =>{
    let checkDataVersionResult = await checkDataVersion();
    if(!checkDataVersionResult.result){
      return resolve({
        error: true,
        message: checkDataVersionResult.message,
      });
    }

    let newDataVersion = `${(new Date()).getTime()}-${Math.floor(Math.random() * (99999 - 10000 + 1) + 10000)}`;

    let clonedMonthlyData = JSON.parse(JSON.stringify(monthlyData));
    let monthId = moment(new Date(clonedDailyDataItem.dateTime)).format('YYYYMM');
    clonedMonthlyData[`${monthId}`] = (parseFloat(clonedMonthlyData[`${monthId}`]) || 0) + difference;
    clonedMonthlyData[`${monthId}`] = parseFloat(clonedMonthlyData[`${monthId}`]);

    let clonedDailyData = JSON.parse(JSON.stringify(dailyData));
    let dateId = moment(new Date(clonedDailyDataItem.dateTime)).format('YYYYMMDD');
    clonedDailyData[`${dateId}`] = clonedDailyDataItem;

    if(clonedDailyData[`${dateId}`].items.length != 0){
      clonedDailyData[`${dateId}`].tags = generateTags(clonedDailyData[`${dateId}`].items);
      clonedDailyData[`${dateId}`].categories = generateCategories(clonedDailyData[`${dateId}`].items);
    }

    db.collection("users").doc(auth.firebaseUser.uid).set({
      monthlyData: clonedMonthlyData,
      dataVersion: newDataVersion,
    }, {merge: true})
    .then(function() {
      if(clonedDailyData[`${dateId}`].items.length == 0){
        db.collection("users").doc(auth.firebaseUser.uid).collection("dailyData").doc(dateId).delete()
        .then(function() {
          dataVersion = newDataVersion;
          delete clonedDailyData[`${dateId}`];
          monthlyData = clonedMonthlyData;
          dailyData = clonedDailyData;

          makeDailyDataList();
          makeMonthlyDataList();

          resolve({ error: false, message: null});
        })
        .catch(function(error) {
          resolve({
            error: true,
            message: "Error removing document: " + error
          });
        });
      }
      else{
        db.collection("users").doc(auth.firebaseUser.uid).collection("dailyData").doc(dateId).set(clonedDailyData[`${dateId}`])
        .then(function() {
          dataVersion = newDataVersion;
          monthlyData = clonedMonthlyData;
          dailyData = clonedDailyData;

          makeDailyDataList();
          makeMonthlyDataList();

          resolve({
            error: false,
            message: null,
          });
        })
        .catch(function(error) {
          resolve({
            error: true,
            message: "Error writing document: " + error
          });
        });
      }      
    })
    .catch(function(error) {
      resolve({
        error: true,
        message: "Error writing document: " + error
      });
    });
  });
}

export function addNewItem(date, name, price, categoryIdList, remark){
  return new Promise(async resolve =>{
    let checkDataVersionResult = await checkDataVersion();
    if(!checkDataVersionResult.result){
      return resolve({
        error: true,
        message: checkDataVersionResult.message,
      });
    }

    let newDataVersion = `${(new Date()).getTime()}-${Math.floor(Math.random() * (99999 - 10000 + 1) + 10000)}`;

    price = parseFloat(price);
    let dateId = moment(date).format('YYYYMMDD');
    let monthId = moment(date).format('YYYYMM');
    let clonedMonthlyData = JSON.parse(JSON.stringify(monthlyData));
    clonedMonthlyData[`${monthId}`] = (parseFloat(clonedMonthlyData[`${monthId}`]) || 0) + price;
    clonedMonthlyData[`${monthId}`] = parseFloat(clonedMonthlyData[`${monthId}`]);

    for(let i = 0; i < itemHistoryData.length; i++){
      if(itemHistoryData[i].name === name){
        itemHistoryData.splice(i, 1);
        break;
      }
    }
    itemHistoryData.unshift({name: name, price: price, categoryIdList: categoryIdList});
    itemHistoryDataList.set(itemHistoryData);

    let clonedDailyData = JSON.parse(JSON.stringify(dailyData));
    if(!clonedDailyData.hasOwnProperty(`${dateId}`)){
      let hasData = false;

      let result = await fetchDateData(dateId);
      if(!result.error){
        if(result.data){
          clonedDailyData[`${dateId}`] = result.data;
          hasData = true;
        }
      }
      else{
        return resolve({
          error: true,
          message: result.message
        });
      }
      
      if(!hasData){
        clonedDailyData[`${dateId}`] = {
          dateId: dateId,
          date: date,
          dateTime: date.getTime(),
          total: 0,
          items: [],
          tags: [],
        }
      }      
    }
    clonedDailyData[`${dateId}`].total = parseFloat(clonedDailyData[`${dateId}`].total) + price;
    clonedDailyData[`${dateId}`].total = parseFloat(clonedDailyData[`${dateId}`].total);
    clonedDailyData[`${dateId}`].items.push({name: name, price: price, categoryIdList: categoryIdList, remark: remark});
    clonedDailyData[`${dateId}`].tags = generateTags(clonedDailyData[`${dateId}`].items);
    clonedDailyData[`${dateId}`].categories = generateCategories(clonedDailyData[`${dateId}`].items);



    db.collection("users").doc(auth.firebaseUser.uid).set({
      monthlyData: clonedMonthlyData,
      itemHistoryData: itemHistoryData,
      dataVersion: newDataVersion,
    }, {merge: true})
    .then(function() {
      db.collection("users").doc(auth.firebaseUser.uid).collection("dailyData").doc(dateId).set(clonedDailyData[`${dateId}`])
        .then(function() {
          dataVersion = newDataVersion;
          monthlyData = clonedMonthlyData;
          dailyData = clonedDailyData;

          makeDailyDataList();
          makeMonthlyDataList();

          resolve({
            error: false,
            message: null,
          });
        })
        .catch(function(error) {
          resolve({
            error: true,
            message: "Error writing document: " + error
          });
        });
      })
    .catch(function(error) {
      resolve({
        error: true,
        message: "Error writing document: " + error
      });
    });
  });
}

function fetchDateData(dateId){
  return new Promise(resolve =>{
    db.collection("users").doc(auth.firebaseUser.uid).collection("dailyData").doc(dateId).get()
    .then(function(doc){
      if(doc.exists && doc.data()){ 
        return resolve({
          error: false,
          data: doc.data()
        });
      }
      else{
        return resolve({
          error: false,
          data: null
        });
      }
    })
    .catch(function(error) {
      return resolve({
        error: true,
        message: "Error writing document: " + error
      });
    });
  });  
}

