export function addNewCategory(category){
  return new Promise(async resolve=>{
    let checkDataVersionResult = await checkDataVersion();
    if(!checkDataVersionResult.result){
      return resolve({
        error: true,
        message: checkDataVersionResult.message,
      });
    }

    let newDataVersion = `${(new Date()).getTime()}-${Math.floor(Math.random() * (99999 - 10000 + 1) + 10000)}`;

    for(let k in categoryData){
      if(categoryData[k] === category.trim()){
        return resolve({
          error: true,
          message: 'Category already exists'
        });
      }
    }

    let clonedCategoryData = JSON.parse(JSON.stringify(categoryData));
    let catId = `${(new Date()).getTime()}-${Math.floor(Math.random() * (99999 - 10000 + 1) + 10000)}`;
    clonedCategoryData[catId] = category;

    db.collection("users").doc(auth.firebaseUser.uid).set({
      categoryData: JSON.stringify(clonedCategoryData),
      dataVersion: newDataVersion,
    }, {merge: true})
    .then(function() {
      dataVersion = newDataVersion;
      categoryData = clonedCategoryData;
      makeCategoryDataList();
      resolve({
        error: false,
      });
    })
    .catch(function(error) {
      resolve({
        error: true,
        message: "Error writing document: " + error
      });
    });
  });
  
}

export function updateCategory(catDataList){
  return new Promise(async resolve=>{
    let checkDataVersionResult = await checkDataVersion();
    if(!checkDataVersionResult.result){
      return resolve({
        error: true,
        message: checkDataVersionResult.message,
      });
    }

    let newDataVersion = `${(new Date()).getTime()}-${Math.floor(Math.random() * (99999 - 10000 + 1) + 10000)}`;

    let tempCategoryData = {};
    for(let i = 0; i < catDataList.length; i++){
      let id = catDataList[i].id;
      let text = catDataList[i].text;
      tempCategoryData[id] = text;
    }

    db.collection("users").doc(auth.firebaseUser.uid).set({
      categoryData: JSON.stringify(tempCategoryData),
      dataVersion: newDataVersion,
    }, {merge: true})
    .then(function() {
      dataVersion = newDataVersion;
      categoryData = tempCategoryData;
      makeCategoryDataList();
      resolve({
        error: false,
      });
    })
    .catch(function(error) {
      resolve({
        error: true,
        message: "Error writing document: " + error
      });
    });
  });
} 

