function makeDailyDataList(){
  let tempList = [];
  Object.keys(dailyData).sort().reverse().forEach(function (k) {
    tempList.push(dailyData[k]);
  });
  dailyDataList.set(tempList);
}

function makeMonthlyDataList(){
  let tempList = [];
  let curYear = '';
  let curYearObj = {};
  Object.keys(monthlyData).sort().reverse().forEach(function (k) {
    // tempList.push(monthlyData[k]);
    if(curYear != k.substr(0,4)){
      curYear = k.substr(0,4);
      curYearObj = {
        year: curYear,
        total: 0,
        totalText: '',
        months: [],
      }
      tempList.push(curYearObj);
    }
    curYearObj.months.push({
      month: k.substr(4,2),
      monthText: moment(k.substr(4,2), 'MM').format('MMMM'),
      total: monthlyData[k],
      totalText: numeral(monthlyData[k]).format('0,0[.]00')
    });
    curYearObj.total += parseFloat(monthlyData[k]);
    curYearObj.totalText = numeral(curYearObj.total).format('0,0[.]00');
  });
  monthlyDataList.set(tempList);
}

function makeCategoryDataList(){
  let tempList = [];

  Object.keys(categoryData).forEach(function (k) {
    tempList.push({ id: k, text: categoryData[k] });
  });

  // for(let k in categoryData){
  //   tempList.push({ id: k, text: categoryData[k] });
  // }

  tempList.sort((a, b) => (a.text > b.text) ? 1 : -1);

  categoryDataList.set(tempList);
}

function generateCategories(items){
  let cats = [];
  for(let i = 0; i < items.length; i++){
    cats = cats.concat(items[i].categoryIdList || []);
  }
  return cats = [...new Set(cats)]
}

function generateTags(items){
  let tags = [];
  for(let i = 0; i < items.length; i++){
    let name = items[i].name;
    tags = tags.concat(stringToTags(name));
  }
  return tags = [...new Set(tags)]
  

  // let resultObj = {};
  // for(let i = 0; i < tags.length; i++){
  //   resultObj[`${tags[i]}`] = true;
  // }
  // return resultObj;
}

function stringToTags(str){
  str = str.toLowerCase();
  str = str.replace(/[.,\/#!$%\^&\*;:{}=\-_`~()\+@\[\]\'\\\"<>\?\|]/g,"");
  let engStr = str.replace(/[^\w\s]/gi, '').trim();
  let nonEngStr = str.replace(/[\w\s]/gi, '').trim();

  let tags = engStr.split(/[\s]+/).concat([...nonEngStr]);
  tags = [...new Set(tags)]; //remove duplicated tag
  tags = tags.filter(function(v) { return `${v}`.trim() != ""; });
  return tags;  
}

