<Page on:pageBeforeIn={onPageBeforeIn} name="monthly-report">
    <Navbar>
      <NavLeft backLink="Back"></NavLeft>
      <NavTitle>{moment(monthId.substr(4,2), 'MM').format('MMMM')} {monthId.substr(0,4)}</NavTitle>
      <NavRight>
        <Link text="Refresh" on:click={()=>{fetchData(true);}}/>
      </NavRight>
    </Navbar>

    <div class="content">

      <!-- <BlockTitle medium>{moment(monthId.substr(4,2), 'MM').format('MMMM')} <span class="year">{monthId.substr(0,4)}</span></BlockTitle> -->

      <BlockTitle large>Total <span class="total">{numeral(grandTotal).format('0,0[.]00')}</span></BlockTitle>

      <div class="chart-container" bind:this={chartContainer}></div>    

      <List inset>
          {#each catTotalList as item}
          <ListItem 
              title={item.categoryId == 'nil' ? 'Uncategorised' : categoryData[item.categoryId]} 
              after={numeral(item.total).format('0,0[.]00')}
              link="#"
              on:click={()=>{ showCategoryFilterPopup(item.categoryId); }}
              >
          </ListItem>
          {/each}
      </List>

      <br><br><br>

      {#if dailyList && dailyList.length > 0}

        <span style="color:#888; margin-left:20px;">Daily</span>
        <!-- <div style="height:1px; width:calc(100% - 40px); margin: 5px auto; background-color:#ccc;"></div> -->
        <!-- <br> -->

        {#each dailyList as item}
          <!-- <BlockTitle>{moment(new Date(item.dateTime)).format("Do")} <span class="total">{numeral(item.total).format('0,0[.]00')}</span></BlockTitle> -->
          <BlockTitle><span style="margin-left:10px">{moment(new Date(item.dateTime)).format("Do")}</span></BlockTitle>
          <List inset>
          {#each item.items as item2}
            <ListItem 
              title={item2.name} 
              after={numeral(item2.price).format('0,0[.]00')}
              >
            </ListItem>
          {/each}
          {#if item.items.length > 1}
            <ListItem 
              header="Total"
              after={numeral(item.total).format('0,0[.]00')}
              >
            </ListItem>
          {/if}
          </List>
        {/each}

      {/if}

      <Popup swipeToClose bind:this={categoryFilterPopup}>
        <Page>
          <Navbar>
            <NavLeft>
              <!-- <a href="#" on:click={()=>{ categoryFilterPopup.close() }} class="link">
                  <i class="icon icon-back"></i>
                  <span>Back</span>
              </a> -->
            </NavLeft>
            <NavTitle sliding>{categoryFilterId == 'nil' ? 'Uncategorised' : categoryData[categoryFilterId]}</NavTitle>
            <NavRight>
              <Link on:click={()=>{ categoryFilterPopup.close() }}>Close</Link>
              <!-- <Link on:click={clearSearchByDate}>Clear</Link> -->
            </NavRight>        
          </Navbar>  

          {#each dailyList as item}
            {#if item.categories.includes(categoryFilterId)}
              <!-- <BlockTitle>{moment(new Date(item.dateTime)).format("Do")} <span class="total">{numeral(item.total).format('0,0[.]00')}</span></BlockTitle> -->
              <BlockTitle><span style="margin-left:10px">{moment(new Date(item.dateTime)).format("Do")}</span></BlockTitle>
              <List inset>
              {#each item.items as item2}
                {#if item2.categoryIdList.includes(categoryFilterId)}
                <ListItem 
                  title={item2.name} 
                  after={numeral(item2.price).format('0,0[.]00')}
                  >
                </ListItem>
                {/if}
              {/each}
              </List>            
            {/if}
          {/each}
        </Page>
      </Popup>
    
    </div>
    
</Page>


<script>
import { f7, Page, Navbar, Block, Link, BlockTitle, List, ListItem, SwipeoutActions, SwipeoutButton, Icon,
ListInput, Popup, ListButton, NavRight, NavLeft, NavTitle, BlockHeader
} from 'framework7-svelte';
import { monthlyReportData, fetchMonthlyReportData, categoryData } from '../js/data';

export let f7route;
export let f7router;


let monthId = f7route.params.monthId;
let chartContainer;

let catTotalList = []; 
let dailyList = [];
let apexChartObj = null;
let grandTotal = 0;

let categoryFilterPopup;
let categoryFilterId = null;

setTimeout(() => {
    fetchData();
}, 100);

async function fetchData(refresh = false){
    let preloader = f7.dialog.preloader('Loading...');
    let result = await fetchMonthlyReportData(monthId, refresh);

    setTimeout(() => {
        preloader.close();

        if(!result.error){
            makeCatTotalList(result.data);
            setTimeout(() => {
                makeChart();
            }, 10);     
            makeDailyList(result.data);   
        }
        else{
            f7.dialog.alert(result.message, 'Alert');
        }
    }, 500);
    
}

function makeCatTotalList(mrData){
    let tempList = [];
    grandTotal = 0;
    Object.keys(mrData).forEach(function (k) {
        let curDateItem = mrData[k];

        for(let i = 0; i < curDateItem.items.length; i++){
            let itemCategoryId = curDateItem.items[i].categoryIdList && curDateItem.items[i].categoryIdList.length > 0 ? curDateItem.items[i].categoryIdList[0] : 'nil' ;
            let hasKey = false;
            for(let j = 0; j < tempList.length; j++){
                if(tempList[j].categoryId == itemCategoryId){
                    hasKey = true; break;
                }
            }

            if(!hasKey){
                tempList.push({
                    categoryId: itemCategoryId,
                    total: parseFloat(curDateItem.items[i].price),
                });
                grandTotal += parseFloat(curDateItem.items[i].price);
            }
            else{
                for(let j = 0; j < tempList.length; j++){
                    if(tempList[j].categoryId == itemCategoryId){
                        tempList[j].total += parseFloat(curDateItem.items[i].price);
                        grandTotal += parseFloat(curDateItem.items[i].price);
                        break;
                    }
                }
            }
        } 
    });
    tempList.sort((a, b) => (a.total > b.total) ? 1 : -1);
    catTotalList = tempList.reverse();
}

function makeDailyList(mrData){
  let tempList = [];
  Object.keys(mrData).sort().reverse().forEach(function (k) {
    tempList.push(mrData[k]);
  });
  dailyList = tempList;
}



function makeChart(){

    let cList = [];
    let tList = [];

    for(let i = 0; i < catTotalList.length; i++){
        cList.push(catTotalList[i].categoryId == 'nil' ? 'Uncategorised' : categoryData[catTotalList[i].categoryId]);
        tList.push(parseFloat(numeral(catTotalList[i].total).format('0[.]00')));        
    }

    let chartHeight = 200;
    if(catTotalList.length > 3){
      chartHeight = 40 * catTotalList.length
    }

    let options = {
          series: [{
          data: tList
        }],
          chart: {
          type: 'bar',
          height: chartHeight,
          toolbar: {
            show: false
            }
        },
        plotOptions: {
          bar: {
            barHeight: '80%',
            // distributed: true,
            horizontal: true,
            dataLabels: {
              position: 'bottom'
            },
          }
        },
        colors: ['#7ac5ff'],
        dataLabels: {
          enabled: true,
          textAnchor: 'start',
          style: {
            colors: ['#00518f']
          },
          formatter: function (val, opt) {
            return opt.w.globals.labels[opt.dataPointIndex] + ': ' + val
          },
          offsetX: 0,
        //   dropShadow: {
        //     enabled: true
        //   }
        },
        stroke: {
          width: 1,
          colors: ['#fff']
        },
        xaxis: {
          categories: cList,
        },
        yaxis: {
          labels: {
            show: false
          }
        },
        // title: {
        //     text: 'Custom DataLabels',
        //     align: 'center',
        //     floating: true
        // },
        // subtitle: {
        //     text: 'Category Names as DataLabels inside bars',
        //     align: 'center',
        // },
        // tooltip: {
        //   theme: 'dark',
        //   x: {
        //     show: false
        //   },
        //   y: {
        //     title: {
        //       formatter: function () {
        //         return ''
        //       }
        //     }
        //   }
        // }
        };

    if(apexChartObj != null)
        apexChartObj.destroy();

    apexChartObj = new ApexCharts(chartContainer, options);

    apexChartObj.render();
}

function showCategoryFilterPopup(catId){
  categoryFilterId = catId;
  categoryFilterPopup.open();
}




function onPageBeforeIn(){
    f7.toolbar.hide('#mainToolbar');
}



</script>

<style>

div.content{
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 600px;
}

span.year{
    /* float: right; */
    color: rgba(0,0,0,0.5);
    font-weight: normal;
    font-size: 14px;
}

div.chart-container{
    width: 100%;
    margin: 0 auto;
    /* max-width: 600px; */
}


span.total{
    float: right;
    color: rgba(0,0,0,0.5);
    font-weight: normal;
}


:global(div[data-name="monthly-report"] .item-title) {
  white-space: normal;
}



</style>
