<Page name="home" 
      ptr 
      onPtrRefresh={onPullToRefresh}
      infinite
      infiniteDistance={50}
      infinitePreloader={showInfinitePreloader}
      onInfinite={loadMore}
      bgColor="white"
      >
  <!-- Top Navbar -->
  <Navbar sliding={false} large>
    <NavLeft>
      <!-- <Link iconIos="f7:menu" iconAurora="f7:menu" iconMd="material:menu" panelOpen="left" /> -->
      <Link iconOnly on:click={openCalendarSelectPopup}>
        <Icon ios="f7:calendar_today" aurora="f7:calendar_today" md="f7:calendar_today">
          {#if fetchDataCriteria.fromDate != null}
            <Badge color="red">!</Badge>
          {/if}
        </Icon>
      </Link>
    </NavLeft>
    <NavTitle sliding>Daily</NavTitle>
    <NavRight>
      <!-- <Link iconIos="f7:list_bullet_below_rectangle" iconAurora="f7:list_bullet_below_rectangle" iconMd="f7:list_bullet_below_rectangle" on:click={openSearchHistoryPopup} /> -->
      <!-- <Link iconIos="f7:list_bullet_below_rectangle" iconAurora="f7:list_bullet_below_rectangle" iconMd="f7:list_bullet_below_rectangle" on:click={()=>{ openSearchCategoryPopup = true; }} /> -->
      <Link iconOnly on:click={()=>{ openSearchCategoryPopup = true; }}>
        <Icon ios="f7:list_bullet_below_rectangle" aurora="f7:list_bullet_below_rectangle" md="f7:list_bullet_below_rectangle">
          {#if searchCategoryIdList.length > 0}
            <Badge color="red">!</Badge>
          {/if}
        </Icon>
      </Link>
    </NavRight>
    <NavTitleLarge>Daily</NavTitleLarge>
    <Subnavbar bgColor="white">
      <Searchbar
        class="home-search-bar"
        bind:this={searchBar}
        customSearch
        onInput={onSearchInput}
        backdrop={false}
        on:searchbarDisable={onSearchClear}
      />
    </Subnavbar>
  </Navbar>

  <Category 
    selectedIds={searchCategoryIdList}
    openSelectPopup={openSearchCategoryPopup} 
    on:selectPopupClose={()=>{ openSearchCategoryPopup = false; }} 
    on:selected={onSearchCategorySelected}
    />


  <!-- Page content -->
  <div class="item-list-container">
  {#each $dailyDataList as item}
    {#if item.items != null && item.items.length > 0}
      <BlockTitle large>
        <span class="date">{moment(new Date(item.dateTime)).format("D")}</span>
        <span class="small-text year">{moment(new Date(item.dateTime)).format("MMM YYYY")} / <span style="color: #aaa">{moment(new Date(item.dateTime)).format("ddd")}</span></span>
      </BlockTitle>
      <span on:click="{() => {f7.views.main.router.navigate('/item-detail/'+item.dateId);}}">
        <!-- <Card outline> -->
        <div class="card card-outline no-shadow no-border" style="background-color: #eee;">
          <CardContent>
            {#each item.items as item2}
              <div style="display: flex; justify-content: space-between; font-size:16px;">
                <div style="flex-shrink: 1; white-space: nowrap; text-overflow: ellipsis; overflow: hidden; max-width:100%; padding-right: 10px;">
                  {item2.name}
                </div>
                <div style="flex-shrink: 0;">{numeral(item2.price).format('0,0[.]00')}</div>
              </div>
            {/each}      
          </CardContent>
          {#if item.items.length > 1}
            <CardFooter>
              <span>Total</span>
              <span style="float: right;">{numeral(item.total).format('0,0[.]00')}</span>
            </CardFooter>
          {/if}
        </div>
        <!-- </Card> -->
      </span>
    {/if}
	{/each}
  </div>

  <Fab color="blue" on:click={openAddNewItemPopup}>
    <Icon ios="f7:plus" aurora="f7:plus" md="material:add"></Icon>
  </Fab>

  <Popup bind:this={addNewItemPopup}>
    <Page>
      <Navbar title="New Item">
        <NavRight>
          <!-- <Link popupClose>Close</Link> -->
          <Link on:click={()=>{ addNewItemPopup.close() }}>Close</Link>
        </NavRight>
      </Navbar>
      <BlockTitle small>
        <span class="item-history-button button" on:click={openItemHistoryPopup}>item history</span>
      </BlockTitle>
      <List>
        <ListInput
          label="Item Name"
          type="text"
          placeholder=""
          bind:value={newItemName}
          onInput={(e)=>newItemName=e.target.value}
        />
        <ListInput
          label="Item Price"
          type="number"
          placeholder=""
          bind:value={newItemPrice}
          onInput={(e)=>newItemPrice=e.target.value}
        />
        <ListInput
          label="Date"
          type="datepicker"
          value={newItemDate}
          onCalendarChange={(v)=>{ newItemDate = v }}
        />
      </List>
      
      <BlockTitle small>Category</BlockTitle>
      <List>
        <ListItem link="#" title={newItemCategoryDisplayText} after="" on:click={()=>{ openCategoryPopup = true; }} />
      </List>

      <BlockTitle small>Remark</BlockTitle>
      <Block strong>
        <textarea class="remark" bind:value={newItemRemark}></textarea>
      </Block>

      <List>
        <ListButton title="Save" on:click={saveNewItem} />
      </List>
    </Page>
  </Popup>

  <Category 
    selectedIds={newItemCategoryIdList}
    openSelectPopup={openCategoryPopup} 
    on:selectPopupClose={()=>{ openCategoryPopup = false; }} 
    on:selected={onCategorySelected}
    />

  <Popup themeDark swipeToClose bind:this={calendarSelectPopup}>
    <Page>
      <Navbar>
        <NavLeft>
          <Link on:click={clearSearchByDate}>Clear</Link>                  
          <!-- <a href="#" class="link popup-close">
              <i class="icon icon-back"></i>
              <span>Back</span>
          </a>   -->
        </NavLeft>
        <NavTitle sliding>Select Date</NavTitle>
        <NavRight>
          <!-- <Link popupClose>Close</Link> -->
          <Link on:click={()=>{ calendarSelectPopup.close() }}>Close</Link>
        </NavRight>        
      </Navbar>
      <!-- <div class="block-title"></div> -->
      <div class="block block-strong no-padding">
        <div id={calendarSelectContainerElId}></div>
      </div>
      <List>
        <ListButton title="Confirm" on:click={searchByDate} />
      </List>
      <!-- <Button on:click={clearSearchByDate}>Clear</Button> -->
      <!-- <Button popupClose>Close</Button> -->
      <!-- <List>
        <ListButton title="Clear" on:click={clearSearchByDate} />
      </List> -->
    </Page>
  </Popup>

  <Popup themeDark swipeToClose bind:this={searchHistoryPopup}>
    <Page>
      <Navbar>
        <NavLeft>
          <a href="#" class="link popup-close">
              <i class="icon icon-back"></i>
              <span>Back</span>
          </a>
        </NavLeft>
        <NavTitle sliding>Search History</NavTitle>
        <NavRight>
          <!-- <Link popupClose>Close</Link> -->
          <!-- <Link on:click={clearSearchByDate}>Clear</Link> -->
        </NavRight>        
      </Navbar>  

      {#if $searchHistoryDataList.length > 0}
        <BlockHeader>Select search history</BlockHeader>
        <List>
        {#each $searchHistoryDataList as item}
          <ListItem title={item} link="#" on:click={()=>{ selectSearchHistory(item) }}></ListItem>
        {/each}
        </List>
        <List>
          <ListButton title="Clear Search History" on:click={onClearSearchHistoryButtonClick} />
        </List>
      {/if}
    </Page>
  </Popup>

  <Popup themeDark swipeToClose bind:this={itemHistoryPopup}>
    <Page>
      <Navbar>
        <NavLeft>
          <!-- <a href="#" on:click={()=>{ itemHistoryPopup.close() }} class="link">
              <i class="icon icon-back"></i>
              <span>Back</span>
          </a> -->
        </NavLeft>
        <NavTitle sliding>Item History</NavTitle>
        <NavRight>
          <!-- <Link popupClose>Close</Link> -->
          <!-- <Link on:click={clearSearchByDate}>Clear</Link> -->
          <Link on:click={()=>{ itemHistoryPopup.close() }}>Close</Link>
        </NavRight>        
      </Navbar>
      <div style="width:100%; height:40px; margin:0px; border-bottom: 1px solid #333;">
        <input type="text" style="width:100%; height:100%; font-size:15px; padding:0px 10px;" placeholder="filter..." 
          on:input={e=>{itemHistoryFilterText = e.target.value;}} value={itemHistoryFilterText} />
      </div>
      
      {#if $itemHistoryDataList.length > 0}
        <BlockHeader>Select item history</BlockHeader>
        <List>
        {#each $itemHistoryDataList.filter(function(item){ return itemHistoryFilterText === '' || item.name.toLowerCase().includes(itemHistoryFilterText.toLowerCase()) }) as item}
          <ListItem title={item.name} after={item.price} link="#" on:click={()=>{ selectItemHistory(item) }}></ListItem>
        {/each}
        </List>
      {/if}
    </Page>
  </Popup>

  
</Page>

<script>
  import {
    f7,
    Page,
    Navbar,
    NavLeft,
    NavTitle,
    NavTitleLarge,
    NavRight,
    Link,
    Toolbar,
    Block,
    BlockTitle,
    List,
    ListItem,
    Row,
    Col,
    Button,
    ListButton,
    Card,
    theme,
    CardHeader,
    CardContent,
    CardFooter,
    Fab,
    Icon,
    Popup,
    ListInput,
    Subnavbar,
    Searchbar,
    BlockHeader,
    Badge,
  } from 'framework7-svelte';

  import { onMount } from 'svelte';
  import appData from '../js/data';
  import { dailyDataList, searchHistoryDataList, itemHistoryDataList, addNewItem, fetchData, noMoreData, deleteAllSearcHistory, categoryDataList } from '../js/data';
  import Category from '../components/category.svelte';

  let addNewItemPopup;
  let newItemName;
  let newItemPrice;
  let newItemDate = [new Date()];
  let newItemCategoryIdList = [];
  const DEFAULT_CATEGORY_DISPLAY_TEXT = 'Select Category...';
  let newItemCategoryDisplayText = DEFAULT_CATEGORY_DISPLAY_TEXT;
  let newItemRemark = '';

  let calendarSelectPopup;
  let calendarSelectContainerElId = 'demo-calendar-inline-container';
  let calendarSelectContainerEl = '#'+calendarSelectContainerElId;
  let calendarF7Instance = null;
  let calendarSelectValue = null;
  let curCalendarSelectValue = null

  let searchHistoryPopup;

  let searchBar;
  let searchBarValue;
  let fetchDataCriteria = {
    search:null,
    fromDate: null,
    toDate: null,
    categories: null,
  }

  let itemHistoryPopup;

  let openCategoryPopup = false;

  let searchCategoryIdList = [];
  let openSearchCategoryPopup = false;

  let itemHistoryFilterText = '';

  async function saveNewItem(){
    if(newItemName == null || newItemName.trim().length == 0){
      f7.dialog.alert('Error: Item name is required.', 'Alert');
      return;
    }
    else if(newItemPrice == null || `${newItemPrice}`.trim().length == 0 || isNaN(newItemPrice)){
      f7.dialog.alert('Error: Item price is required and should be number.', 'Alert');
      return;
    }
    else if(newItemDate[0] == null){
      f7.dialog.alert('Error: Date is required.', 'Alert');
      return;
    }
    else if(newItemRemark.length > 500){
      f7.dialog.alert('Error: Remark is more than 500 characters.', 'Alert');
      return;
    }

    let preloader = f7.dialog.preloader('Loading...');
    let result = await addNewItem(newItemDate[0], newItemName, newItemPrice, newItemCategoryIdList, newItemRemark);
    preloader.close();
    if(!result.error){
      addNewItemPopup.close();
    }      
    else{
      f7.dialog.alert(result.message, 'Alert');
    }
  }

  function onSearchCategorySelected(event){
    let categoryList = event.detail;
    searchCategoryIdList = [];
    for(let i = 0; i < categoryList.length; i++){
      searchCategoryIdList.push(categoryList[i].id);
    }

    fetchDataCriteria.categories = searchCategoryIdList;
    let preloader = f7.dialog.preloader();
    fetchData(fetchDataCriteria, true).then(()=>{
      setTimeout(() => {
        preloader.close() 
      },  333);          
    }); 
  }

  function onCategorySelected(event){
    let categoryList = event.detail;
    newItemCategoryDisplayText = '';
    newItemCategoryIdList = [];
    for(let i = 0; i < categoryList.length; i++){
      newItemCategoryDisplayText += categoryList[i].text;
      if(i != categoryList.length - 1 && categoryList[i].text != null && categoryList[i].text.trim() != '')
        newItemCategoryDisplayText += ', ';
      newItemCategoryIdList.push(categoryList[i].id);
    }
    if(newItemCategoryDisplayText == '')
      newItemCategoryDisplayText = DEFAULT_CATEGORY_DISPLAY_TEXT;
  }

  function getCategoryTextList(catIdList){
    let result = '';
    if(catIdList != null){
      for(let i = 0; i < catIdList.length; i++){
        for(let j = 0; j < $categoryDataList.length; j++){
          if(catIdList[i] == $categoryDataList[j].id){          
            result += $categoryDataList[j].text;
          }
        }
        if(i != catIdList.length - 1 && catIdList[i].text != null && catIdList[i].text.trim() != '')
          result += ', ';
      }
    }
    return result;
  }

  function makeCategoryDisplayText(){
    let str = getCategoryTextList(newItemCategoryIdList);
    if(str == '')
      newItemCategoryDisplayText = DEFAULT_CATEGORY_DISPLAY_TEXT;
    else
      newItemCategoryDisplayText = str;
  }

  function openAddNewItemPopup(){
    newItemName = null;
    newItemPrice = null;
    newItemDate = [new Date()];
    newItemCategoryIdList = [];
    newItemCategoryDisplayText = DEFAULT_CATEGORY_DISPLAY_TEXT;
    newItemRemark = '';
    addNewItemPopup.open();
  }

  function openSearchHistoryPopup(){
    searchHistoryPopup.open();
  }

  function openItemHistoryPopup(){
    itemHistoryFilterText = '';
    itemHistoryPopup.open();
  }
  
  function openCalendarSelectPopup(){
    calendarSelectPopup.open();
    if(calendarF7Instance == null){
      var monthNames = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August' , 'September' , 'October', 'November', 'December'];
      calendarF7Instance = f7.calendar.create({
        containerEl: calendarSelectContainerEl,
        value: [new Date()],
        weekHeader: false,
        rangePicker: true,
        dateFormat: 'yyyy/m/d',
        monthSelector: true,
        renderToolbar: function () {
          return '<div class="toolbar calendar-custom-toolbar no-shadow">' +
            '<div class="toolbar-inner">' +
              '<div class="left">' +
                '<a href="#" class="link icon-only"><i class="icon icon-back ' + (app.theme === 'md' ? 'color-black' : '') + '"></i></a>' +
              '</div>' +
              '<div class="center"></div>' +
              '<div class="right">' +
                '<a href="#" class="link icon-only"><i class="icon icon-forward ' + (app.theme === 'md' ? 'color-black' : '') + '"></i></a>' +
              '</div>' +
            '</div>' +
          '</div>';
        },
        on: {
          init: function (c) {
            f7.$('.calendar-custom-toolbar .center').text(monthNames[c.currentMonth] +', ' + c.currentYear);
            f7.$('.calendar-custom-toolbar .left .link').on('click', function () {
              calendarF7Instance.prevMonth();
            });
            f7.$('.calendar-custom-toolbar .right .link').on('click', function () {
              calendarF7Instance.nextMonth();
            });
          },
          monthYearChangeStart: function (c) {
            f7.$('.calendar-custom-toolbar .center').text(monthNames[c.currentMonth] +', ' + c.currentYear);
          },
          change: function(c, v){
            calendarSelectValue = v;
          },
        }
      });
    }
    calendarF7Instance.setValue(curCalendarSelectValue || null);
  }

  function searchByDate(){
    curCalendarSelectValue = calendarSelectValue;
    calendarSelectPopup.close();
    console.log(curCalendarSelectValue);

    fetchDataCriteria.fromDate = null;
    fetchDataCriteria.toDate = null;
    if(curCalendarSelectValue != null && curCalendarSelectValue.length > 0){
      fetchDataCriteria.fromDate = moment(curCalendarSelectValue[0]).format('YYYYMMDD');      
    }
    if(curCalendarSelectValue != null && curCalendarSelectValue.length > 1){
      fetchDataCriteria.toDate = moment(curCalendarSelectValue[1]).format('YYYYMMDD');     
    }
    let preloader = f7.dialog.preloader();
    fetchData(fetchDataCriteria, true).then(()=>{
      setTimeout(() => {
        preloader.close() 
      },  333);          
    }); 
  }

  function clearSearchByDate(){
    calendarSelectValue = null;
    calendarF7Instance.setValue(calendarSelectValue);
    // calendarSelectPopup.close();
  }

  function onSearchInput(e){
    searchBarValue = e.target.value;
  }

  function onSearchClear(){
    if(fetchDataCriteria.search == null || fetchDataCriteria.search.trim() === '')
      return;
    fetchDataCriteria.search = null;
    let preloader = f7.dialog.preloader();
    fetchData(fetchDataCriteria, true).then(()=>{
      setTimeout(() => {
        preloader.close() 
      },  333);          
    });    
  }

  function selectSearchHistory(value){
    searchBar.search(value);
    searchHistoryPopup.close();
    f7.$('.home-search-bar input[type="search"]').focus();
  }

  function selectItemHistory(item){
    newItemName = item.name;
    newItemPrice = item.price;
    newItemCategoryIdList = item.categoryIdList || [];
    makeCategoryDisplayText();
    itemHistoryPopup.close();
  }

  async function onPullToRefresh(done){
    await fetchData(fetchDataCriteria, true);
    setTimeout(() => {
      done();
    }, 333); 
  }

  let showInfinitePreloader = false;
  let loadingMore = false;
  async function loadMore(){
    if(!noMoreData){
      if(!loadingMore){
        loadingMore = true;
        showInfinitePreloader = true;

        await fetchData(fetchDataCriteria);

        loadingMore = false;
        showInfinitePreloader = false;
      }
    }    
  }

  function onClearSearchHistoryButtonClick(){
    f7.dialog.confirm('', 'Do you want to clear search history?', ()=>{
      deleteAllSearcHistory();
    })
  }

  onMount(() => {
    f7.$('.home-search-bar input[type="search"]').on('keypress', function (e) {
      if(e.key === 'Enter'){
        console.log(searchBarValue);        
        f7.$('.home-search-bar input[type="search"]').blur();
        fetchDataCriteria.search = searchBarValue;
        let preloader = f7.dialog.preloader();
        fetchData(fetchDataCriteria, true).then(()=>{
          setTimeout(() => {
            preloader.close() 
          },  333);          
        });     
      }
      else{
      }
    });    
	});

</script>

<style>
.item-list-container{
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 600px;

  padding-bottom: 50px;
  padding-right: 5px;
  padding-left: 5px;
  box-sizing: border-box;
}
.item-history-button{
  float: right;
}
span.small-text.year{
  font-size: 12px;
  color: rgba(0,0,0,.5);
}
span.date{
  /* padding: 4px 12px 4px 16px; */
  padding: 4px 0px 4px 0px;
  width: 40px;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  background-color: #ccc;
  border-radius: 15px 5px 5px 5px;
  border-radius: 5px 5px 5px 5px;
}

textarea.remark{
  width: 100%;
  height: 80px;
  resize: none;
}



:global(.view-main .navbar-bg) {
		background-color: #fff;
}

:global(.view-main .navbar-bg:after) {
		background-color: #fff;
}
:global(.view-main .subnavbar:after) {
		background-color: #fff;
}

