<Page on:pageBeforeIn={onPageBeforeIn} bgColor="white" name="item-detail" >
  {#if thisItem != null}
    <Navbar>
      <NavLeft backLink="Back"></NavLeft>
      <NavTitle>{moment(new Date(thisItem.dateTime)).format("D MMMM YYYY / ddd")}</NavTitle>
      <NavRight>
        <Link text="Add" on:click={onAddButtonClick}/>
      </NavRight>
    </Navbar>
  
    <BlockTitle large>Total <span class="total">{numeral(thisItem.total).format('0,0[.]00')}</span></BlockTitle>
    {#if thisItemList.length > 0}
    <BlockTitle small><span style="color:rgba(0,0,0,0.25);">swipe left to delete, right to edit</span></BlockTitle>
    <List mediaList>
      {#each thisItemList as item}
      <ListItem 
          noChevron 
          swipeout
          title={item.name} 
          footer="{getCategoryTextList(item.categoryIdList)}"
          after={item.price} >
          <SwipeoutActions right>
              <SwipeoutButton bgColor="red" close overswipe onClick={()=>onDeleteButtonClick(item)}><Icon f7="trash_fill" />Delete</SwipeoutButton>
          </SwipeoutActions>
          <SwipeoutActions left>
              <SwipeoutButton bgColor="blue" close overswipe onClick={()=>onEditButtonClick(item)}><Icon f7="square_pencil" />Edit</SwipeoutButton>
          </SwipeoutActions>
      </ListItem>
      {/each}
    </List>
    {/if}


    <Popup bind:this={editItemPopup}>
      <Page>
        <Navbar title={isAddingItem ? 'Add Item' : 'Edit Item'}>
          <NavRight>
            <!-- <Link popupClose>Close</Link> -->
            <Link on:click={()=>{ editItemPopup.close() }}>Close</Link>
          </NavRight>
        </Navbar>
        {#if isAddingItem}
        <BlockTitle small>
          <span class="item-history-button button" on:click={openItemHistoryPopup}>item history</span>
        </BlockTitle>
        {/if}
        <List>
          <ListInput
            label="Item Name"
            type="text"
            placeholder=""
            value={editItemName}
            onInput={(e)=>editItemName=e.target.value}
          />
          <ListInput
            label="Item Price"
            type="number"
            placeholder=""
            value={editItemPrice}
            onInput={(e)=>editItemPrice=e.target.value}
          />        
        </List>
        <BlockTitle small>Category</BlockTitle>
        <List>
          <ListItem link="#" title={editItemCategoryDisplayText} after="" on:click={()=>{ openCategoryPopup = true; }} />
        </List>
        <BlockTitle small>Remarks</BlockTitle>
        <Block strong>
          <textarea class="remark" bind:this={remarkTextArea} bind:value={editItemRemark} on:keyup={()=>{textAreaAdjust()}}></textarea>
        </Block>
        <!-- <div class="remark">
          <textarea>aasdasdhagsdasdg</textarea>
        </div>         -->
        <List>
          <ListButton title="Save" on:click={saveEditItem} />
        </List>
      </Page>
    </Popup>

    <Category 
      selectedIds={editItemCategoryIdList}
      openSelectPopup={openCategoryPopup} 
      on:selectPopupClose={()=>{ openCategoryPopup = false; }} 
      on:selected={onCategorySelected}
      />

    <Popup themeDark swipeToClose bind:this={itemHistoryPopup}>
      <Page>
        <Navbar>
          <NavLeft>
            <!-- <a href="#" on:click={()=>{ itemHistoryPopup.close() }} class="link">
                <i class="icon icon-back"></i>
                <span>Back</span>
            </a> -->
          </NavLeft>
          <NavTitle sliding>Item History</NavTitle>
          <NavRight>
            <!-- <Link popupClose>Close</Link> -->
            <!-- <Link on:click={clearSearchByDate}>Clear</Link> -->
            <Link on:click={()=>{ itemHistoryPopup.close() }}>Close</Link>
          </NavRight>        
        </Navbar>  
        <div style="width:100%; height:40px; margin:0px; border-bottom: 1px solid #333;">
        <input type="text" style="width:100%; height:100%; font-size:15px; padding:0px 10px;" placeholder="filter..." 
            on:input={e=>{itemHistoryFilterText = e.target.value;}} value={itemHistoryFilterText} />
        </div>

        {#if $itemHistoryDataList.length > 0}
          <BlockHeader>Select item history</BlockHeader>
          <List>
          {#each $itemHistoryDataList.filter(function(item){ return itemHistoryFilterText === '' || item.name.toLowerCase().includes(itemHistoryFilterText.toLowerCase()) }) as item}
            <ListItem title={item.name} after={item.price} link="#" on:click={()=>{ selectItemHistory(item) }}></ListItem>
          {/each}
          </List>
        {/if}
      </Page>
    </Popup>
  {/if}

  <!-- <Block strong>
    <ul>
      <li><b>Url:</b> {f7route.url}</li>
      <li><b>Path:</b> {f7route.path}</li>
      <li><b>Hash:</b> {f7route.hash}</li>
      <li><b>Params:</b>
        <ul>
          {#each Object.keys(f7route.params) as key}
            <li><b>{key}:</b> {f7route.params[key]}</li>
          {/each}
        </ul>
      </li>
      <li><b>Query:</b>
        <ul>
          {#each Object.keys(f7route.query) as key}
            <li><b>{key}:</b> {f7route.query[key]}</li>
          {/each}
        </ul>
      </li>
      <li><b>Route:</b> {f7route.route.path}</li>
    </ul>
  </Block>
  <Block strong>
    <Link onClick={() => f7router.back()}>Go back via Router API</Link>
  </Block> -->


</Page>
<script>
  import { f7, Page, Navbar, Block, Link, BlockTitle, List, ListItem, SwipeoutActions, SwipeoutButton, Icon,
    ListInput, Popup, ListButton, NavRight, NavLeft, NavTitle, BlockHeader
  } from 'framework7-svelte';
  import { dailyDataList, getItemByDate, editItem, addNewItem, deleteItem, itemHistoryDataList, categoryDataList } from '../js/data';
  import Category from '../components/category.svelte';

  export let f7route;
  export let f7router;

  let thisItem = getItemByDate(f7route.params.date);
  let thisItemList = thisItem.items;

  let isAddingItem = false;
  let editItemPopup;
  let editItemName;
  let editItemPrice;
  let editItemCategoryIdList = [];
  const DEFAULT_CATEGORY_DISPLAY_TEXT = 'Select Category...';
  let editItemCategoryDisplayText = DEFAULT_CATEGORY_DISPLAY_TEXT;
  let editItemRemark = '';
  let editItemObj;

  let itemHistoryPopup;

  let openCategoryPopup = false;

  let remarkTextArea;

  let itemHistoryFilterText = '';

  function onEditButtonClick(item){
    isAddingItem = false;
    editItemName = item.name;
    editItemPrice = item.price;
    editItemCategoryIdList = JSON.parse(JSON.stringify(item.categoryIdList || []));
    makeCategoryDisplayText();
    editItemRemark = item.remark || '';
    editItemObj = item;
    setTimeout(() => {      
      textAreaAdjust();
    }, 100);
    editItemPopup.open();
  }

  function onAddButtonClick(){
    isAddingItem = true;
    editItemName = '';
    editItemPrice = null;
    editItemCategoryIdList = [];
    makeCategoryDisplayText();
    editItemRemark = '';
    editItemObj = {};
    editItemPopup.open();
  }

  function onDeleteButtonClick(item){
    f7.dialog.confirm('', 'Are you sure you want to delete this item?', async()=>{
        let preloader = f7.dialog.preloader();

        let backupThisItem = JSON.parse(JSON.stringify(thisItem));
        let itemName = item.name;
        let itemPrice = parseFloat(item.price);
        
        thisItem.total -= itemPrice;
        thisItemList.splice(thisItemList.indexOf(item),1);
        let clonedThisItem = JSON.parse(JSON.stringify(thisItem));
        let difference = itemPrice * -1;
        
        thisItem = backupThisItem;
        
        let result = await deleteItem(clonedThisItem, difference);
        preloader.close();

        if(!result.error){
          thisItem = getItemByDate(f7route.params.date);
          if(thisItem == null)
            f7router.back();
          else
            thisItemList = thisItem.items;
        }
        else{
          f7.dialog.alert(result.message, 'Alert');
        }
        
    });      
  }

  async function saveEditItem(){

    if(editItemName == null || editItemName.trim().length == 0){
      f7.dialog.alert('Error: Item name is required.', 'Alert');
      return;
    }
    else if(editItemPrice == null || `${editItemPrice}`.trim().length == 0 || isNaN(editItemPrice)){
      f7.dialog.alert('Error: Item price is required and should be number.', 'Alert');
      return;
    }
    else if(editItemRemark.length > 500){
      f7.dialog.alert('Error: Remark is more than 500 characters', 'Alert');
      return;
    }

    let preloader = f7.dialog.preloader();
    
    let result = null;
    if(!isAddingItem){ //edit
      let oldName = editItemObj.name;
      let oldPrice = parseFloat(editItemObj.price);
      let oldCategoryIdList = editItemObj.categoryIdList;
      let oldRemark = editItemObj.remark;

      editItemObj.name = editItemName;
      editItemObj.price = parseFloat(editItemPrice);
      editItemObj.categoryIdList = editItemCategoryIdList;
      editItemObj.remark = editItemRemark;

      let clonedThisItem = JSON.parse(JSON.stringify(thisItem));
      editItemObj.name = oldName;
      editItemObj.price = oldPrice;
      editItemObj.categoryIdList = oldCategoryIdList;
      editItemObj.remark = oldRemark;

      let difference = parseFloat(editItemPrice) - oldPrice;

      clonedThisItem.total += difference;

      result = await editItem(clonedThisItem, difference);    
    }
    else{ //add
      let clonedThisItem = JSON.parse(JSON.stringify(thisItem));
      editItemObj.name = editItemName;
      editItemObj.price = parseFloat(editItemPrice);
      editItemObj.categoryIdList = editItemCategoryIdList;
      editItemObj.remark = editItemRemark;
      clonedThisItem.items.push(editItemObj);
      let difference = parseFloat(editItemPrice);

      clonedThisItem.total += difference;

      result = await editItem(clonedThisItem, difference, editItemObj); 
    }

    preloader.close();

    if(!result.error){
      thisItem = getItemByDate(f7route.params.date);
      thisItemList = thisItem.items;
      editItemPopup.close();
    }
    else{
      f7.dialog.alert(result.message, 'Alert');
    }   
  }

  function onCategorySelected(event){
    let categoryList = event.detail;
    editItemCategoryDisplayText = '';
    editItemCategoryIdList = [];
    for(let i = 0; i < categoryList.length; i++){
      editItemCategoryDisplayText += categoryList[i].text
      if(i != categoryList.length - 1 && categoryList[i].text != null && categoryList[i].text.trim() != '')
        editItemCategoryDisplayText += ', ';
      editItemCategoryIdList.push(categoryList[i].id);
    }
    if(editItemCategoryDisplayText == '')
      editItemCategoryDisplayText = DEFAULT_CATEGORY_DISPLAY_TEXT;
  }

  function makeCategoryDisplayText(){
    let str = getCategoryTextList(editItemCategoryIdList);
    if(str == '')
      editItemCategoryDisplayText = DEFAULT_CATEGORY_DISPLAY_TEXT;
    else
      editItemCategoryDisplayText = str;
  }

  function getCategoryTextList(catIdList){
    let result = '';
    if(catIdList != null){
      for(let i = 0; i < catIdList.length; i++){
        for(let j = 0; j < $categoryDataList.length; j++){
          if(catIdList[i] == $categoryDataList[j].id){          
            result += $categoryDataList[j].text;
          }
        }
        if(i != catIdList.length - 1 && catIdList[i].text != null && catIdList[i].text.trim() != '')
          result += ', ';
      }
    }
    return result;
  }

  function onPageBeforeIn(){
    f7.toolbar.hide('#mainToolbar');
  }


  function openItemHistoryPopup(){
    itemHistoryFilterText = '';
    itemHistoryPopup.open();
  }

  function selectItemHistory(item){
    editItemName = item.name;
    editItemPrice = item.price;
    editItemCategoryIdList = item.categoryIdList || [];
    makeCategoryDisplayText();
    itemHistoryPopup.close();
  }

  function textAreaAdjust() {
    remarkTextArea.style.height = "1px";
    remarkTextArea.style.height = (25+remarkTextArea.scrollHeight)+"px";
  }




</script>

<style>
span.total{
    float: right;
    color: rgba(0,0,0,0.5);
    font-weight: normal;
}

.item-history-button{
  float: right;
}


textarea.remark{
  width: 100%;
  min-height: 80px;
  min-height: 20px;
  resize: none;
  overflow:hidden
}

:global(div[data-name="item-detail"] .item-title) {
  white-space: normal;
}
</style>
