module.exports = {
    subscribe: subscribe,
    unsubscribe: unsubscribe,
    send: send,
}

var messageSubscribeData = {};

function subscribe(message, callback) {
    if (!messageSubscribeData[message])
        messageSubscribeData[message] = [];
    if (!messageSubscribeData[message].includes(callback))
        messageSubscribeData[message].push(callback);
}

function unsubscribe(message, callback) {
    if(messageSubscribeData[message]){
        var index = messageSubscribeData[message].indexOf(callback);
        if (index > -1) {
            messageSubscribeData[message].splice(index, 1);
        }
    }    
}

function send(message, data = null){
    if(messageSubscribeData[message]){
        for(let i = 0; i < messageSubscribeData[message].length; i++){
            messageSubscribeData[message][i](data);
        }
    }  
}

