<App params={ f7params } >

  <!-- Left panel with cover effect-->
  <Panel left reveal themeDark>
    <View>
      <Page>
        <Navbar title="Left Panel"/>
        <Block>Left panel content goes here</Block>
      </Page>
    </View>
  </Panel>


  <!-- Right panel with reveal effect-->
  <Panel right reveal themeDark>
    <View>
      <Page>
        <Navbar title="Right Panel"/>
        <Block>Right panel content goes here</Block>
      </Page>
    </View>
  </Panel>


  <!-- Views/Tabs container -->
  <Views tabs class="safe-areas">
    <!-- Tabbar for switching views-tabs -->
    <Toolbar id="mainToolbar" tabbar labels bottom>
      <Link tabLink="#view-home" tabLinkActive iconIos="f7:calendar_today" iconAurora="f7:calendar_today" iconMd="f7:calendar_today" text="Daily" />
      <Link tabLink="#view-monthly" iconIos="f7:chart_pie" iconAurora="f7:chart_pie" iconMd="f7:chart_pie" text="Monthly" />
      <Link tabLink="#view-settings" iconIos="f7:gear_alt" iconAurora="f7:gear_alt" iconMd="material:settings" text="Settings" />
    </Toolbar>

    <!-- Your main view/tab, should have "view-main" class. It also has "tabActive" prop -->
    <View id="view-home" main tab tabActive url="/" />

    <!-- Monthly View -->
    <View id="view-monthly" name="monthly" tab url="/monthly/" />

    <!-- Settings View -->
    <View id="view-settings" name="settings" tab url="/settings/" />
  </Views>


  <!-- Popup -->
  <Popup id="my-popup">
    <View>
      <Page>
        <Navbar title="Popup">
          <NavRight>
            <Link popupClose>Close</Link>
          </NavRight>
        </Navbar>
        <Block>
          <p>Popup content goes here.</p>
        </Block>
      </Page>
    </View>
  </Popup>

  <LoginScreen bind:this={splashScreen}>
    <View>
      <div class="page splash-page">
        <div class="page-content login-screen-content">
          <div class="my-splash-screen">
            <div class="center">
              <!-- <i class="fas fa-money-check-alt splash-icon"></i> -->
              <i class="f7-icons splash-icon">square_stack_3d_down_right_fill</i>
              <br><br>
              <span>EXPENSE<br><br>v{version}</span>
              <br><br><br><br>
            </div>            
            <div class="splash-preloader"><Preloader></Preloader></div>
          </div>
        </div>
      </div>
    </View>
  </LoginScreen>

  <LoginScreen bind:this={loginScreen}>
    <View>
      <Page loginScreen>
        {#if !isShowSignUp}
          <LoginScreenTitle>LOGIN</LoginScreenTitle>
          <br><br>
          <List form>
            <ListInput
              type="email"
              name="email"
              label="Email"
              placeholder="enter your email"
              value={email}
              onInput={(e) => email = e.target.value}
            />
            <ListInput
              type="password"
              name="password"
              label="Password"
              placeholder="enter your password"
              value={password}
              onInput={(e) => password = e.target.value}
            />
          </List>
          <br><br>
          <List>
            <Row>
              <Col width="50">
                <Button onClick={() => login()} large >Login</Button>
              </Col>
              <Col width="50">
                <Button onClick={() => { isShowSignUp = true; }} color="gray" large>Sign Up</Button>
              </Col>
            </Row>
          </List>
        {:else}
          <LoginScreenTitle>SIGN UP</LoginScreenTitle>
          <br><br>
          <List form>
            <ListInput
              type="email"
              name="email"
              label="Email"
              placeholder="enter your email"
              value={email}
              onInput={(e) => email = e.target.value}
            />
            <ListInput
              type="password"
              name="password"
              label="Password"
              placeholder="enter your password"
              value={password}
              onInput={(e) => password = e.target.value}
            />
            <ListInput
              type="password"
              name="confirm_password"
              label="Confirm Password"
              placeholder="enter your password again"
              value={password2}
              onInput={(e) => password2 = e.target.value}
            />
          </List>
          <br><br>
          <List>
            <Row>
              <Col width="50">
                <Button onClick={() => signup()} large>Sign Up</Button>
              </Col>
              <Col width="50">
                <Button onClick={() => { isShowSignUp = false; }} color="gray" large>Login</Button>
              </Col>
            </Row>
          </List>
        {/if}
        <br>
        <BlockFooter>
          EXPENSE<br />v{version}<br><br><br><br>
        </BlockFooter>
      </Page>
    </View>
  </LoginScreen>
</App>
<script>
  import { onMount } from 'svelte';
  import config from '../js/config.json';
  import auth from '../js/auth';
  import { fetchData } from '../js/data';
  import MessageCenter from '../js/message-center';

  import {
    f7,
    f7ready,
    App,
    Panel,
    Views,
    View,
    Popup,
    Page,
    Navbar,
    Toolbar,
    NavRight,
    Link,
    Block,
    BlockTitle,
    LoginScreen,
    LoginScreenTitle,
    List,
    ListItem,
    ListInput,
    ListButton,
    BlockFooter,
    Row, Col, Button,
    Preloader,
    Icon,
  } from 'framework7-svelte';


  import routes from '../js/routes';

  // Framework7 Parameters
  let f7params = {
    name: 'my-expense-web-app', // App name
    theme: 'ios', // Automatic theme detection
    // App root data
    data: function () {
      return {

        // Demo products for Catalog section
        products: [
          {
            id: '1',
            title: 'Apple iPhone 8',
            description: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Nisi tempora similique reiciendis, error nesciunt vero, blanditiis pariatur dolor, minima sed sapiente rerum, dolorem corrupti hic modi praesentium unde saepe perspiciatis.'
          },
          {
            id: '2',
            title: 'Apple iPhone 8 Plus',
            description: 'Velit odit autem modi saepe ratione totam minus, aperiam, labore quia provident temporibus quasi est ut aliquid blanditiis beatae suscipit odio vel! Nostrum porro sunt sint eveniet maiores, dolorem itaque!'
          },
          {
            id: '3',
            title: 'Apple iPhone X',
            description: 'Expedita sequi perferendis quod illum pariatur aliquam, alias laboriosam! Vero blanditiis placeat, mollitia necessitatibus reprehenderit. Labore dolores amet quos, accusamus earum asperiores officiis assumenda optio architecto quia neque, quae eum.'
          },
        ]
      };
    },

    // App routes
    routes: routes,
  };

  let version = config.version;

  // Login screen demo data
  let email = '';
  let password = '';
  let password2 = '';
  let loginScreen;
  let isShowSignUp = false;

  let splashScreen;
  let splashScreenShown = false;

  function login(){
    let error = false;
    if(!email || !password){
      f7.dialog.alert('please enter email and password', 'Alert');
      error = true;
    }
    if(!error){
      f7.dialog.preloader('please wait...');
      auth.login(email, password, (err, msg)=>{      
        f7.dialog.close();
        if(err){
          f7.dialog.alert(msg, 'Alert');
        }
      });
    }
    
  }

  function signup(){
    let error = false;
    if(!email || !password){
      f7.dialog.alert('please enter email and password', 'Alert');
      error = true;
    }
    if(password != password2){
      f7.dialog.alert('please confirm your password', 'Alert');
      error = true;
    }
    if(!error){
      f7.dialog.preloader('please wait...');
      auth.signUp(email, password, (err, msg)=>{
        f7.dialog.close();
        if(err){
          f7.dialog.alert(msg, 'Alert');
        }
      });
    }
  }

  function showSignUp(){
    isShowSignUp = true;
  }

  onMount(() => {
    f7ready(() => {
      // Call F7 APIs here    

      splashScreen.instance().open(false);
      splashScreenShown = true;

      MessageCenter.subscribe('AUTH:ON_USER_LOGGED_IN', data => {
        loginScreen.instance().close();
        // let preloader2 = f7.dialog.preloader('Loading...');
        fetchData({}, true).finally(()=> {
          // preloader2.close()
          if(splashScreenShown){
            splashScreen.instance().close();
            splashScreenShown = false;
          }
        });
      });

      MessageCenter.subscribe('AUTH:ON_USER_LOGGED_OUT', data => {
        // loginScreen.instance().open();
        location.reload();
      });


      // let preloader = f7.dialog.preloader('Loading...');      

      //wait for firebase ready
      setTimeout(() => {        
        if(!auth.isLoggedin){
          loginScreen.instance().open();
          splashScreen.instance().close();
          splashScreenShown = false;
        }          
        // preloader.close();        
      }, 1000);

      auth.init();
      
    });
  })
</script>

<style>

:global(body) {
  /* this will apply to <body> */
  font-family:  '-apple-system','HelveticaNeue', Helvetica, "Microsoft JhengHei", Arial, sans-serif !important;
}

.splash-page,
.splash-page .page-content{
  height: 100%;
  width: 100%;
  position: relative;
}

.my-splash-screen{
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;

  font-size: 16px;
  background-color: rgb(230, 230, 230);
  background-color: #fff;
  color: rgb(46, 46, 46);
}
.my-splash-screen .center{
  align-self: center;
  text-align: center;
}

.my-splash-screen .splash-icon{
  font-size: 25vh;
}

.splash-preloader{
  position: absolute;
  bottom: 50px;
  left: 0px;
  width: 100%;
  text-align: center;
}
</style>