<Popup 
    themeDark 
    swipeToClose 
    bind:this={selectPopup}
    opened={openSelectPopup} 
    onPopupClosed={onSelectPopupClosed}
    onPopupOpen={onSelectPopupOpen}
    >
    <Page>
        <Navbar>
        <NavLeft>
            {#if $categoryDataList.length > 0}
                <Link on:click={toggleEditMode}>Edit</Link>
            {/if}
            <!-- <a href="#" on:click={closeSelectPopup} class="link">
                <i class="icon icon-back"></i>
                <span>Back</span>
            </a> -->
        </NavLeft>
        <NavTitle sliding>Category</NavTitle>
        <NavRight>
            <!-- <Link popupClose>Close</Link> -->
            <Link on:click={()=>{ selectPopup.close() }}>Close</Link>
        </NavRight>        
        </Navbar>

        {#if editMode}
            {#if editCategoryDataList.length > 0}            
                <BlockTitle>Edit category</BlockTitle>
                <BlockHeader>To delete a category, just leave the field blank</BlockHeader>
                <List>
                {#each editCategoryDataList as item}
                    <ListInput
                        value={item.text}
                        placeholder={item.text}
                        onInput={(e) => item.text = e.target.value}
                        >
                    </ListInput>
                {/each}
                </List>

                <List>
                    <ListButton title="Save" on:click={saveEdit} />
                </List>
            {/if}
        {:else}
            {#if $categoryDataList.length > 0}            
                <BlockHeader>Select category <span class="clear button color-gray" on:click={clearSelected}>Clear</span></BlockHeader>
                <List>
                {#each $categoryDataList as item}
                    <ListItem 
                        checkbox 
                        title={item.text} 
                        checked={isChecked(item.id, _selectedIds)}
                        value={item.id}
                        onChange={onCheckBoxChange}
                        />
                {/each}
                <ListItem link="#" after="+ More" noChevron on:click={_addNewCategory} />
                </List>

                <List>
                    <ListButton title="Confirm" on:click={confirmSelectCategory} />
                </List>
            {:else}
                <List>
                    <ListButton title="Add Category" on:click={_addNewCategory} />
                </List>
            {/if}
        {/if}  

        
    </Page>
</Popup>

<script>
  import {
    f7,
    Page,
    Navbar,
    NavLeft,
    NavTitle,
    NavTitleLarge,
    NavRight,
    Link,
    Toolbar,
    Block,
    BlockTitle,
    List,
    ListItem,
    Row,
    Col,
    Button,
    ListButton,
    Card,
    theme,
    CardHeader,
    CardContent,
    CardFooter,
    Fab,
    Icon,
    Popup,
    ListInput,
    Subnavbar,
    Searchbar,
    BlockHeader,
    Badge,
    } from 'framework7-svelte';

    import { onMount, createEventDispatcher } from 'svelte';
    import appData from '../js/data';
    import { categoryDataList, addNewCategory, updateCategory } from '../js/data';

    //props
    export let openSelectPopup;
    export let selectedIds;

    let _selectedIds;

    const dispatch = createEventDispatcher();

    let selectPopup;

    let editMode = false;
    let editCategoryDataList;

    function closeSelectPopup(){
        selectPopup.close()
    }

    function onSelectPopupClosed(){
        openSelectPopup = false;
        dispatch('selectPopupClose');
    }

    function onSelectPopupOpen(){
        editMode = false;
        _selectedIds = JSON.parse(JSON.stringify(selectedIds));
        $categoryDataList = $categoryDataList;
    }

    function _addNewCategory(){
        f7.dialog.prompt('Category', 'Add', async(v)=>{
            if(v == null || v.trim() == ''){
                f7.dialog.alert('Category cannot be empty', 'Alert');
                return;
            }
            let preloader = f7.dialog.preloader();
            let result = await addNewCategory(v);
            preloader.close();
            if(result.error){
                f7.dialog.alert(result.message, 'Alert');
            }
        })
    }

    function confirmSelectCategory(){
        let resultData = [];
        for(let i = 0; i < _selectedIds.length; i++){
            for(let j = 0; j < $categoryDataList.length; j++){
                if(_selectedIds[i] == $categoryDataList[j].id){
                    resultData.push($categoryDataList[j]);
                }
            }
        }
        dispatch('selected', resultData);
        closeSelectPopup();
    }

    function isChecked(catId){
        if(_selectedIds != null){
            for(let i = 0; i < _selectedIds.length; i++){
                if(_selectedIds[i] == catId)
                    return true;
            }
        }
        return false;
    }

    function onCheckBoxChange(e){
        var value = e.target.value;
        if (e.target.checked) {
            _selectedIds.push(value);
        } else {
            _selectedIds.splice(_selectedIds.indexOf(value), 1);
        }
        _selectedIds = _selectedIds;
    }

    function toggleEditMode(){
        editCategoryDataList = JSON.parse(JSON.stringify($categoryDataList));
        editMode = !editMode
    }

    async function saveEdit(){
        for(let i = editCategoryDataList.length - 1; i >= 0 ; i--){
            if(editCategoryDataList[i].text == null || editCategoryDataList[i].text.trim() == ''){
                editCategoryDataList.splice(i,1);
            }
        }

        for(let i = 0; i < editCategoryDataList.length; i++){
            for(let j = 0; j < editCategoryDataList.length; j++){
                if(i != j && editCategoryDataList[i].text == editCategoryDataList[j].text){
                    f7.dialog.alert("Error: duplicated category", 'Alert');
                    return;
                }
            }
        }

        let preloader = f7.dialog.preloader();
        let result = await updateCategory(editCategoryDataList);
        preloader.close();
        if(result.error){
            f7.dialog.alert(result.message, 'Alert');
        }
        editMode = false;
    }

    function clearSelected(){
        _selectedIds = [];
    }

  

</script>

<style>
.clear{
    float: right;
}

</style>