<Page name="settings">
  <Navbar sliding={false} large>
      <NavLeft>
      </NavLeft>
      <NavTitle sliding>Settings</NavTitle>
      <NavRight>
      </NavRight>
      <NavTitleLarge>Settings <span style="font-size: 12px; opacity: .33">v{version}</span></NavTitleLarge>
  </Navbar>
  <BlockHeader>Currency Converter</BlockHeader>
  <List>
    {#each $currencyConverterDataList as item}
      <ListButton title={`${item.from} to ${item.to}`} on:click={()=> openCurrencyConverter(item)} />
    {/each}
    <ListItem title="Edit Converter" link="/currency-converter/" />
  </List>
  <List>
    <ListButton title="Refresh" on:click={()=>{location.reload();}} />
  </List>
  <List>
    <ListButton title="Logout" on:click={onLogoutButtonClick} />
  </List>
</Page>
<script>
  import {
    f7,
    Page,
    Navbar,
    List,
    ListInput,
    ListItem,
    ListButton,
    Toggle,
    BlockTitle,
    BlockHeader,
    Row,
    Button,
    Range,
    Block,
    NavLeft,
    NavRight,
    NavTitle,
    NavTitleLarge,
  } from 'framework7-svelte';

  import auth from '../js/auth';
  import { currencyConverterDataList } from '../js/data';
  import config from '../js/config.json';

  let version = config.version;

  function onLogoutButtonClick(){
    f7.dialog.confirm('', 'Logout?', ()=>{
      auth.logout();
    })    
  }
  
  function openCurrencyConverter(item){
    f7.dialog.prompt(`${item.from} to ${item.to}`, null, (value)=>{
      f7.dialog.alert(`${item.from} ${value} = ${item.to} ${numeral(value * item.rate).format('0,0[.]00')}`, `${item.from} to ${item.to}`);
    });
  }

</script>
